  obtenerUsuario(rut:any): Usuario {
    this.lst_usuarios.forEach(element => {
      if (element.rut == rut){
        this.usuario = element;
      }
    });
    return this.usuario;
  }
  agregarUsuario(usuario:Usuario){
    this.lst_usuarios.push(usuario);
  }
  eliminarUsuario(rut:any){
    for(let i=0; i < this.lst_usuarios.length;i++){
      if(this.lst_usuarios[i].rut == rut){
        this.lst_usuarios.splice(i,1);
      }
    }
  }
  obtenerSistemas(){
    return this.sistemas;
  }
  obtenerSubSistemas(){
    return this.sub_sistemas;
  }
  obtenerSistemasUsuario(rut:any){
    let lstSistemasUsuario:any[] = [];
    this.sistemasUsuario.forEach(element => {
        if (element.rut == rut){
          lstSistemasUsuario.push(element);
        }
    });
    return lstSistemasUsuario;
  }
  agregarSistemaUsuiario(newSys:any){
    this.sistemasUsuario.push(newSys);
  }
  obtenerSistemaPorNombre(nom:any){
    let ok:any;
    this.sistemas.forEach(element => {
      if(element.nom_sistema == nom){
        ok = element;
      }
    });
    return ok;
  }
  obtenerSubSistemaPorNombre(nom:any){
    let ok:any;
    this.sub_sistemas.forEach(element => {
      if(element.nom_sub_sistema == nom){
        ok = element;
      }
    });
    return ok;
  }
  eliminarSistemaUsuario(nom_sys:any){
    for(let i=0; i < this.sistemasUsuario.length;i++){
      if(this.sistemasUsuario[i].nom_sistema == nom_sys){
        this.sistemasUsuario.splice(i,1);
      }
    }
  }

  editarUsuario(oldU:any,newU:Usuario){
    for(let i=0; i < this.lst_usuarios.length;i++){
      if(this.lst_usuarios[i].rut == oldU){
       this.lst_usuarios[i].apellidos = newU.apellidos;
       this.lst_usuarios[i].correo = newU.correo;
       this.lst_usuarios[i].nom_usuario = newU.nom_usuario;
       this.lst_usuarios[i].nombres = newU.nombres;
       this.lst_usuarios[i].rut = newU.rut;
      } 
    }
  }
cambiarClave(rut:any,newPass:any){
  for(let i=0; i < this.lst_usuarios.length;i++){
    if(this.lst_usuarios[i].rut == rut){
     this.lst_usuarios[i].clave = newPass;
    } 
  }
}
bloquearUsuario(rut:any){
  for(let i=0; i < this.lst_usuarios.length;i++){
    if(this.lst_usuarios[i].rut == rut){
     this.lst_usuarios[i].bloqueado = true; 
    } 
  }
}
