import { Component, OnInit } from '@angular/core';
import { ServicioService } from '../service/servicios.service';
import { ResourceLoader } from '@angular/compiler';
import { Usuario } from '../entidades/usuario';
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-busqueda',
  templateUrl: './busqueda.component.html',
  styleUrls: ['./busqueda.component.css']
})
export class BusquedaComponent implements OnInit {
  public frmRegistro: FormGroup;
  public usuarios:Usuario[] = [];
  public rclave:any="";
  public brut:any="";
  public busuario:any="";
  public bnombre:any="";
  public bapellidos:any="";
  public usuario_agregar:Usuario = new Usuario("","","","","","","",false);
  constructor(private userService:ServicioService, private fb:FormBuilder,private router:Router) {
    this.frmRegistro = this.fb.group({
      nombres:       ['', Validators.required],
      nom_usuario:   ['', Validators.required],
      hom_usuario:   ['', Validators.required],
      apellidos:     ['', Validators.required],
      correo:        ['', Validators.required],
      clave:         ['', Validators.required],
      rclave:        ['', Validators.required],
      rut:           ['', Validators.required],
    });
  }

  ngOnInit() {
    this.obtenerUsuarios();
  }
  obtenerUsuarios(){
    this.usuarios = this.userService.obtenerUsuarios();
  }
  informacionUsuario(rut:any){
    this.router.navigateByUrl('/informacion?rut='+rut);
  }
  agregarUsuario(rut:any){
    this.userService.agregarUsuario(this.usuario_agregar);
    this.router.navigateByUrl('/informacion?rut='+rut);
    
  }
  eliminarUsuario(rut:any){
    this.userService.eliminarUsuario(rut);
  }
  limpiarBusqueda(){
    this.brut ="";
    this.bapellidos = "";
    this.bnombre = "";
    this.busuario = "";
    this.obtenerUsuarios();
  }
  buscarUsuario(){
    if(this.brut == "" || this.bapellidos == "" || this.bnombre == "" || this.bnombre == ""){
      this.obtenerUsuarios();
    }else{

    }
  }
}



