import { Component, OnInit, transition } from '@angular/core';
import {  Router, ActivatedRoute, Params  } from '@angular/router';
import { ServicioService } from '../service/servicios.service';
import { Usuario } from '../entidades/usuario';

@Component({
  selector: 'app-informacion-usuario',
  templateUrl: './informacion-usuario.component.html',
  styleUrls: ['./informacion-usuario.component.css']
})
export class InformacionUsuarioComponent implements OnInit {
  public rut:any;
  public errorSistema:any = false;
  public usuario:Usuario;
  public lstSistemas:any;
  public lstSubSistemas:any;
  public selectSys:any;  
  public lstSistemasUsuario:any;
  public sistema:any;
  public subSistema:any;
  public sistemaSeleccionado:any="";
  public subSistemaSeleccionado:any="";
  public sistemaUsuario :any = {rut:"",nom_sistema:"",CUP:"",nom_sub_sistema:"",descripcion:""}

  public newNombres:any="";
  public newApellidos:any="";
  public newNomUsuario:any="";
  public newRut:any="";
  public newCorreo:any="";

  public newPass:any="";
  public rNewPass:any="";
  public actualPass:any="";
  constructor(private activatedRoute: ActivatedRoute, private userService:ServicioService,private router:Router) { }

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe((params: Params) => {
      this.rut =  params['rut'];
      this.obtenerUsuario(this.rut);
      this.obtenerSistemasUsuario(this.rut);
    });
    this.obtenerSistemas();
    this.obtenerSubSistemas();
  }
  volver(){
    console.log(this.rut);
    this.router.navigateByUrl('/busqueda');
  }
  obtenerUsuario(rut:any){
    this.usuario =  this.userService.obtenerUsuario(rut);
    console.log(this.usuario);
  }
  obtenerSistemas(){
    this.lstSistemas = this.userService.obtenerSistemas();
    console.log(this.lstSistemas);
  }
  obtenerSistemaPorNombre(nom:any){

  }
  obtenerSubSistemas(){
    this.lstSubSistemas = this.userService.obtenerSubSistemas();
  }
  obtenerSistemasUsuario(rut:any){
    this.lstSistemasUsuario = this.userService.obtenerSistemasUsuario(rut);
  }
  agregarSistemaUsuario(){
    if(this.sistemaSeleccionado != "" && this.subSistemaSeleccionado != ""){
      this.sistemaUsuario.rut = this.rut;
      this.sistemaUsuario.nom_sistema = "nom_sistema";
      this.sistemaUsuario.CUP = "CUP"
      this.sistemaUsuario.nom_sub_sistema = "nom_sub_sistema";
      this.sistemaUsuario.descripcion = "descripcion";
  
      this.userService.agregarSistemaUsuiario(this.sistemaUsuario);
      this.obtenerSistemasUsuario(this.rut);
      this.errorSistema = false;
    }else
    {
      this.errorSistema = true;
    }
  
  }
  obtenerSistema(event:any){
 
   this.sistema =  this.userService.obtenerSistemaPorNombre(event.target.value);
    console.log(this.sistema);
    console.log(this.sistemaSeleccionado);
  }
  obtenerSubsistema(event:any){
    this.subSistema =  this.userService.obtenerSubSistemaPorNombre(event.target.value);
    console.log(this.subSistema);
  }
  eliminarSistemaUsuario(nom_sys:any ){
    console.log(nom_sys);
    this.userService.eliminarSistemaUsuario(nom_sys);
    this.obtenerSistemasUsuario(this.rut);
  }

  eliminarUsuario(rut:any){
    this.userService.eliminarUsuario(rut);
    this.volver();
  }
  cambiarClave(){
    let newUsuarioPass = this.newPass;
    console.log(this.newPass,this.rut);
    this.userService.cambiarClave(this.rut,newUsuarioPass);
    this.actualPass ="";
    this.newPass = "";
    this.rNewPass = "";
  }
  bloquearUsuario(){
    this.userService.bloquearUsuario(this.rut);
    let newUsuarioBloqueado:Usuario = new Usuario("","","","","","","",true);
    
  }
  editarUsuario(){
    let newUsuario:Usuario = new Usuario(this. newNombres,this.newNomUsuario,"",this.newApellidos,this.newCorreo,"",this.newRut,"");
    this.userService.editarUsuario(this.rut,newUsuario);
    this.router.navigateByUrl('/informacion?rut='+this.newRut);
  }
}



